import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import view360 from '../views/360-view/360-view'
import caseResearch from '../views/case-research/case-research'
import patientOnline from '../views/patient-online/patient-online'
import patientAll from '../views/patient-online/patient-all'
import patientMe from '../views/patient-online/patient-me'

import systemMangeto from '../views/system-mangeto/system-mangeto'
import inspectionReport from '../views/system-mangeto/inspection-report'
import sleepDurtor from '../views/system-mangeto/sleep-ductor'


Vue.use(VueRouter)

const routes = [{
		path: '/',
		name: 'patientOnline',
		component: patientOnline,
		redirect: "/patientAll",
	},
	{
		path: '/about',
		name: 'about',
		component: () => import( /* webpackChunkName: "about" */ '../views/About.vue')
	},
	{
		path: '/view360',
		name: 'view360',
		component: view360
	},
	{
		path: '/caseResearch',
		name: 'caseResearch',
		component: caseResearch
	},
	{
		path: '/patientOnline',
		name: 'patientOnline',
		component: patientOnline,
		redirect: "/patientAll",
		children: [{
			path: '/patientAll',
			name: 'patientAll',
			component: patientAll
		},
		{
			path: '/patientMe',
			name: 'patientMe',
			component: patientMe
		}]
	},
	{
		path: '/systemMangeto',
		name: 'systemMangeto',
		component: systemMangeto,
		redirect: "/inspectionReport",
		children: [{
			path: '/inspectionReport',
			name: 'inspectionReport',
			component: inspectionReport
		},{
			path: '/sleepDurtor',
			name: 'sleepDurtor',
			component: sleepDurtor
		}]
	},
	
]

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
})

export default router
